import empty from "@/components/empty";
import Json from '@/Json';
import api from '@/common/index.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import '@/common/uni.css';
let mvc = {
	loadingType: 'more', //加载更多状态
	orderList: [],
	cartoon: false,
	navList: [],
	activeClass: 0,
	page: 1, //当前页
	type: 'add',
	status: 0
}
export default {
	components: {
		uniNavBar,
		empty,
		uniLoadMore,
	},
	data() {
		return {
			mvc: {
				loadingType: 'more', //加载更多状态
				orderList: [],
				cartoon: false,
				navList: [],
				activeClass: 0,
				page: 1, //当前页
				type: 'add',
				status: 0
			}
		};
	},

	onLoad(options) {


	},
	onShow() {
		let that = this;
		setTimeout(function() {
			mvc = that.mvc;
			mvc.activeClass=0;
			mvc.page=1;
			mvc.orderList=[];
			api.load(that.$t('index').loadmsg)
			api.pageTitle(that.$t('order_list').title)
			mvc.navList = that.$t('order_list').navList;
			that.loadData();
		}, 300)
	},
	computed: {
		i18n() {
			return this.$t('order_list')
		}
	},

	//下拉刷新
	onPullDownRefresh() {
		mvc.page = 1;
		mvc.type = 'refresh';
		this.loadData();
	},
	//加载更多
	onReachBottom() {
		mvc.type = 'add'
		this.loadData();

	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		//获取未确认订单列表
		//加载商品 ，带下拉刷新和上滑加载
		async loadData() {
			let that = this;
			mvc.cartoon = false;
			let params = {
				page: mvc.page
			}
			if (mvc.activeClass == 0) {
				that.$ajax.orderHa(params).then(res => {
					if (res.list == null || res.other.totalPage == 0) {
						uni.hideLoading();
						mvc.loadingType = "nomore";
						mvc.cartoon = true;
						return false;
					} else {
						that.pagenum(res);
					}
				})
			} else {
				that.$ajax.orderHd(params).then(res => {
					if (res.list == null || res.other.totalPage == 0) {
						uni.hideLoading();
						mvc.loadingType = "nomore";
						mvc.cartoon = true;
						return false;
					} else {
						that.pagenum(res);
					}
				})
			}
		},
		pagenum(res) {
			let that = this;
			let orderList = res.list;
			mvc.loadingType = res.other.thispage > res.other.totalPage ? 'nomore' : 'more';
			//请求分页
			if (mvc.type == 'add') {
				mvc.page = mvc.page + 1;

				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc
					return;
				}
				mvc.loadingType = 'loading';
			} else {
				mvc.loadingType = 'more'
			}

			// 刷新页面
			if (mvc.type == 'refresh') {
				mvc.orderList = [];
				uni.stopPullDownRefresh();
				mvc.page = mvc.page + 1;

				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc
					return;
				}
				mvc.loadingType = 'loading';
			}
			// 当前页面小于20条
			if (orderList.length < 20) {
				mvc.loadingType = 'nomore';
				uni.hideLoading();
				mvc.orderList = mvc.orderList.concat(orderList);
				that.mvc = mvc
				return false;
			}
			uni.hideLoading();
			mvc.orderList = mvc.orderList.concat(orderList);
			that.mvc = mvc
		},

		bindnav(index) {
			mvc.activeClass = index;
			let that = this;
			mvc.cartoon = false;
			mvc.loadingType = "more";
			mvc.page = 1;
			mvc.orderList = [];
			api.load(this.$t('index').loadmsg)
			that.loadData();

		},
		details(e) {
			if (mvc.activeClass == 0) {
				uni.navigateTo({
					url: '/pages/oreder-detail/index?sn=' + e
				})
			};
			if (mvc.activeClass == 1) {
				uni.navigateTo({
					url: '/pages/order-hddata/order-hddata?sn=' + e
				})
			};

		},
	},
}
